<template>
    <div class="account-name-doEdit">
        <mt-cell title="取款账号:"
                 v-model="infoData.account"></mt-cell>
        <mt-cell title="取款编号:"
                 v-model="infoData.list"></mt-cell>
        <mt-cell title="预约金额:"
                 v-model="infoData.money"></mt-cell>
        <mt-cell title="预约日期:"
                 v-model="infoData.date"></mt-cell>
        <mt-cell title="预约状态:"
                 v-model="infoData.status"></mt-cell>
        <mt-cell title="温馨提示:"
                 v-model="infoData.info"
                 type="textarea"></mt-cell>
    
        <div class="field-action">
            <mt-button type="primary"
                       size="small"
                       v-if="flag"
                       @click="doCancel">取消预约</mt-button>
            <mt-button type="primary"
                       size="small"
                       @click="$back">返回</mt-button>
        </div>
    
    </div>
    
    </div>
</template>

<script>
//获取一卡通账户
import * as serviceAccount from '../../services/account';
export default {
    name: 'account-name-doEdit',
    data() {
        return {
            infoData: {
                account: '',
                name: '',
                money: '',
                list: '',
                date: '',
                status: '',
                info: '仅显示当日及未使用预约记录，最多20条记录'

            }
        };
    },
    computed: {
        flag() {
            return this.infoData.status === "未使用" ? true : false
        }
    },
    methods: {
        doCancel() {
            this.$router.push({ name: 'noCardMsg', params: { go: '-4' } });
        }
    },
    created() {
        this.$store.dispatch('showLoading', false);
        this.infoData = Object.assign({}, this.infoData, this.$route.params.info);
    }
};
</script>



